export default function TableView() {
  const [selectedStatus, setSelectedStatus] = useState("all");
  const [selectedNames, setSelectedNames] = useState([]);
  const [startDate, setStartDate] = useState("");
  const [endDate, setEndDate] = useState("");
  const [creditOfficerData, setCreditOfficerData] = useState([]);
  const [lastUpdatedAt, setLastUpdatedAt] = useState("");
  const [isLoading, setIsLoading] = useState("");
  const [error, setError] = useState("");

  const isCreditOfficerSelected = (creditOfficer) =>
    (creditOfficer?.status === selectedStatus || selectedStatus === "all") &&
    (selectedNames.includes(creditOfficer.staff_name) ||
      selectedNames.length === 0);

  const fetchCreditOfficersData = () => {
    setIsLoading("Fetching report...");
    let formData = new FormData();
    if (startDate && endDate) {
      formData.append("from_date", startDate);
      formData.append("to_date", endDate);
    }

    axios
      .post(
        "/Loan_portfolio_report/get_credit_officers_report",
        formData
      )
      .then((response) => {
        if (response.data) {
          let updatedAt = moment
            .duration(moment().diff(moment(response.data.date_modified)))
            .humanize();
          console.log(response.data.date_modified);
          setLastUpdatedAt(updatedAt);
          let responseData = response.data;
          setCreditOfficerData(responseData.data);
          // console.log(creditOfficerData)
          setIsLoading("");
        } else {
          throw new Error(
            "Something Went wrong while fetching credit officers report"
          );
        }
      })
      .catch((err) => {
        console.log(err);
        setError("Something Went wrong while fetching credit officers report");
        setIsLoading("");
      });
  };
  const computeCreditOfficersData = (fromDate, toDate) => {
    if (fromDate && toDate) {
      setIsLoading(
        "Calculating figures and generating report. Please be patient, this might take a while"
      );
      let formData = new FormData();
      formData.append("from_date", fromDate);
      formData.append("to_date", toDate);

      axios
        .post(
          "/Loan_portfolio_report/compute_credit_officers_report",
          formData
        )
        .then(({ data }) => {
          if (data.success) {
            fetchCreditOfficersData();
            setIsLoading("");
          }
        })
        .catch((err) => {
          console.log(err);
          setError(
            "Something Went wrong while generating credit officers report"
          );
          setIsLoading("");
        });
    } else {
      fetchCreditOfficersData();
    }
  };

  useEffect(() => {
    console.log(startDate + " - " + endDate);
    computeCreditOfficersData(startDate, endDate);
  }, [startDate, endDate]);

  return (
    <Card>
      {error && (
        <div className="shadow-lg alert alert-error">
          <div>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              className="flex-shrink-0 w-6 h-6 stroke-current"
              fill="none"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"
              />
            </svg>
            <span>Error! {error}</span>
          </div>
        </div>
      )}
      {isLoading && (
        <div className="bg-black opacity-60 w-full h-full flex flex-col justify-center items-center absolute top-0 bottom-0 left-0 right-0 z-[1000]">
          <div className="flex justify-center py-2 font-bold text-white">
            {isLoading}
          </div>
          <div>
            <progress className="w-56 progress"></progress>
          </div>
        </div>
      )}
      <div className="flex justify-end p-1">
        <span>Last updated {lastUpdatedAt} ago</span>
      </div>
      <div className="hidden sm:mt-6 sm:flex sm:justify-start sm:space-x-2">
        <MultiSelectBox
          handleSelect={(value) => setSelectedNames(value)}
          placeholder="Select Credit Officers"
          maxWidth="max-w-xs"
        >
          {creditOfficerData?.map((item) => (
            <MultiSelectBoxItem
              key={item.staff_name}
              value={item.staff_name}
              text={item.staff_name}
            />
          ))}
        </MultiSelectBox>
        {/* <Dropdown
          maxWidth="max-w-xs"
          defaultValue="all"
          handleSelect={(value) => setSelectedStatus(value)}
        >
          <DropdownItem value="all" text="All Performances" />
          <DropdownItem value="overperforming" text="Overperforming" />
          <DropdownItem value="average" text="Average" />
          <DropdownItem value="underperforming" text="Underperforming" />
        </Dropdown> */}
        <Datepicker
          placeholder="Select Date range"
          enableRelativeDates={true}
          handleSelect={(startDate, endDate) => {
            setStartDate(moment(startDate).format("YYYY-MM-DD"));
            setEndDate(moment(endDate).format("YYYY-MM-DD"));
          }}
          defaultStartDate={null}
          defaultEndDate={null}
          minDate={null}
          maxDate={null}
          color="blue"
          maxWidth="max-w-none"
          marginTop="mt-0"
        />
      </div>

      <div className="mt-6 space-y-2 sm:hidden sm:space-y-0">
        <MultiSelectBox
          handleSelect={(value) => setSelectedNames(value)}
          placeholder="Select credit officer"
          maxWidth="max-w-full"
        >
          {creditOfficerData?.map((item) => (
            <MultiSelectBoxItem
              key={item.staff_name}
              value={item.staff_name}
              text={item.staff_name}
            />
          ))}
        </MultiSelectBox>
        {/* <Dropdown
          maxWidth="max-w-full"
          defaultValue="all"
          handleSelect={(value) => setSelectedStatus(value)}
        >
          <DropdownItem value="all" text="All Performances" />
          <DropdownItem value="overperforming" text="Overperforming" />
          <DropdownItem value="average" text="Average" />
          <DropdownItem value="underperforming" text="Underperforming" />
        </Dropdown> */}
        <Datepicker
          placeholder="Select Date range"
          enableRelativeDates={true}
          handleSelect={(startDate, endDate) => {
            setStartDate(moment(startDate).format("YYYY-MM-DD"));
            setEndDate(moment(endDate).format("YYYY-MM-DD"));
          }}
          defaultStartDate={null}
          defaultEndDate={null}
          minDate={null}
          maxDate={null}
          color="blue"
          maxWidth="max-w-none"
          marginTop="mt-0"
        />
      </div>

      <Table marginTop="mt-6">
        <TableHead>
          <TableRow>
            <TableHeaderCell>Credit Officer</TableHeaderCell>
            <TableHeaderCell textAlignment="text-right">Loans</TableHeaderCell>
            <TableHeaderCell textAlignment="text-right">
              Disbursed
            </TableHeaderCell>
            <TableHeaderCell textAlignment="text-right">
              Expected Interest
            </TableHeaderCell>
            <TableHeaderCell textAlignment="text-right">
              Principle recovered
            </TableHeaderCell>
            <TableHeaderCell textAlignment="text-right">
              Interest collected
            </TableHeaderCell>
            <TableHeaderCell textAlignment="text-right">
              <Flex>Principal At Risk</Flex>
              <Flex>
                <small className="text-xs font-thin">
                  (Defaulted Principal)
                </small>
              </Flex>
            </TableHeaderCell>
            <TableHeaderCell textAlignment="text-right">
              <Flex>Interest At Risk</Flex>
              <Flex>
                <small className="text-xs font-thin">
                  (Defaulted Interest)
                </small>
              </Flex>
            </TableHeaderCell>
            {/* <TableHeaderCell textAlignment="text-right">Status</TableHeaderCell> */}
          </TableRow>
        </TableHead>

        <TableBody>
          {creditOfficerData
            ?.filter((item) => isCreditOfficerSelected(item))
            .map((item) => (
              <TableRow key={item.staff_name}>
                <TableCell>{item.staff_name}</TableCell>
                <TableCell textAlignment="text-right">
                  {Intl.NumberFormat().format(item.loan_count)}
                </TableCell>
                <TableCell textAlignment="text-right">
                  {Intl.NumberFormat().format(item.total_disbursed)}
                </TableCell>
                <TableCell textAlignment="text-right">
                  {Intl.NumberFormat().format(item.total_interest_expected)}
                </TableCell>
                <TableCell textAlignment="text-right">
                  {Intl.NumberFormat().format(item.total_paid_principal)}
                </TableCell>
                <TableCell textAlignment="text-right">
                  {Intl.NumberFormat().format(item.total_paid_interest)}
                </TableCell>
                <TableCell textAlignment="text-right">
                  {Intl.NumberFormat().format(item.principal_in_demand)}
                </TableCell>
                <TableCell textAlignment="text-right">
                  {Intl.NumberFormat().format(item.interest_in_demand)}
                </TableCell>
                {/* <TableCell textAlignment="text-right">
                  <BadgeDelta deltaType="unchanged" text="average" size="xs" />
                </TableCell> */}
              </TableRow>
            ))}
        </TableBody>
      </Table>
    </Card>
  );
}
